html {
  /* font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif; */
  color: #333333;
}

.flex{
  display: flex;
}

.flex--gap{
  gap: 0.5rem;
}

.header {
  height: 4rem;
  padding-left: 2rem;
  background-color: #89375f;
}

.header__title {
  min-width: 24rem;
  max-width: 60%;
  margin: 0 auto;

  position: relative;
  top: -3rem;

  color: #ffffff;
  font-size: 1.5rem;
  font-weight: bold;
}
.header__title::after{
  content: "Mobile";
}

.content-wrapper {
  min-width: 21rem;
  max-width: 50%;
  margin: 0 auto;
}

.tile {
  max-width: 30rem;
  padding: 1.25rem 0.5rem;
  margin: 0 auto;
  margin-bottom: 1.75rem;
  border-top: 1px solid;
  border-bottom: 1px solid;
  border-color: #e0e0e0;
}



.tile-list{
  margin-top: 1rem;
}

.tile__image{
  margin-bottom: 1rem;
}

.tile__image > img {
  border-radius: 0.4rem;
  width: 100%;
  height: 7.5rem;
  object-fit: cover;
  object-position: center 50%;
}

.tile__title{
  padding-bottom: 0.5rem;
  border-top: none;
  border-right: none;
  border-bottom: 1px solid;
  border-left: none;
  border-color: #e0e0e0;
}


.tile__star::after {
    content: url('../public/icons/icon-star.svg');
    display: block;
    height: 1rem;
    width: 1rem;
  
}

.tile__title > h2 {
  font-size: 1.5rem;
  font-weight: 700;
  margin: 0.25rem 0;
}
.tile__title__location{
  color: #757575;
  font-size: 0.9rem;
  font-weight: 500;
}
.tile__details{
  padding-top: 0.5rem;
}

.tile__details__capacity{
  margin-bottom: 1rem;
  font-size: 1rem;
}
.tile__details__ratings{
  gap: 1rem;
  color:#757575;
  font-size: 0.8rem;
  font-weight: 600;
  margin-bottom: 1rem;
}
.tile__details__hygiene{
  width: 100%;
  border-right: 1px solid #E0E0E0;
}
.tile__details__hygiene:only-child{
  border-right: none;
}
.tile__details__hygiene>span{
  padding-top: 2px;
}

.tile__details__hygiene-icon::after {
  content: url('../public/icons/icon-hygiene.svg');
  display: block;
  height: 1.5rem;
  width: 1.5rem;
}

.tile__details__rating{
  width: 100%
}

.tile__details__grade{
  color: #ffffff;
  font-size: 1rem;
  display: inline-block;
  background-color: #89375f;
  padding: 0 0.5rem ;
  border-radius: 5px;
}


.tile__details__rating > span{
  padding-top: 2px;
}


.tile__link-details{
  margin-bottom: 1rem;
}
.tile__link-details > a{
  color: #159895;
  font-weight: 700;
}
.tile__link-details > a:hover{
  color: rgba(21, 152, 149, 0.8);
  font-weight: 700;
}

.tile__price_details{
  text-align: right;
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
}



@media (min-width: 990px) {
  .header__title::after{
    content: "Desktop";
  }

  .content-wrapper{
    max-width: 80%;
  }
  .tile{
    border: 1px solid #E0E0E0;
    padding: 1.25rem;
    max-width: 60rem;
    display: grid;
    grid-template-columns: 13rem 3fr 1fr;
    gap: 1rem; 
  }

.tile-data-wrapper{
  width: 80%;
}

  .tile__link-details{
    height: 1rem;
  }

  .tile__image {
    margin: 0;
  }
  .tile__image > img {
    width: 100%;
    height: 13rem !important;
  }

  .price-button-wrapper{
    align-self: end;
  }
}
